@import url(https://fonts.googleapis.com/css?family=Gloria+Hallelujah);
body {
  background-color: #ffe589;
  margin: auto, 0;
}

.card-container {
  display: inline-block;
  border: 6px solid #ffe589;
  background: #ffe589;
}

.card-container.clicked .flipper {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.flipper {
  -webkit-transition: 0.4s;
  transition: 0.4s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.front,
.back {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  left: 0;
  top: 0;
}

.back {
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
}

.front {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.counter-box {
  background: #fffad3;
  margin-left: 5px;
  margin-top: 5px;
  width: 31%;
  height: 14.3%;
}

.container {
  position: relative;
  width: 80%;
  left: 10%;
  line-height: 100%;
}

body {
  display: flex;
}

.board {
  width: 0px;
  height: 0px;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
}

body {
  justify-content: center;
  text-align: center;
  background-color: #89c5ffb5;
  padding: 1em 1em;
  font-family: "Gloria Hallelujah", cursive;
}

h1 {
  margin-top: 30px;
}

.logo {
  height: 45vh;
  padding-top: 10px;
  padding-bottom: 10px;
  -webkit-animation: rotation 15s infinite linear;
          animation: rotation 15s infinite linear;
}

.start-button p {
  margin: 0 auto;
  padding-top: 10px;
  padding-bottom: 10px;
}

.start-button {
  font-family: "Gloria Hallelujah", cursive;
  font-size: 20px;
  width: 20vw;
  background: #5d6acb;
  border: none;
  border-radius: 45px;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  cursor: pointer;
  outline: none;
  text-transform: uppercase;
}

@-webkit-keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}

@keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}

.play-again-button {
  font-family: "Gloria Hallelujah", cursive;
  font-size: 17px;
  width: 20vw;
  background: #faf2d6;
  border: none;
  border-radius: 45px;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  cursor: pointer;
  outline: none;
  text-transform: uppercase;
}

/* unvisited link */
a:link {
  color: black;
}

/* visited link */
a:visited {
  color: black;
}

/* mouse over link */
a:hover {
  color: orange;
}

/* selected link */
a:active {
  color: black;
}

